<template>
  <el-row class="detail-row">
    <el-col :span="14" class="left">{{metric}}</el-col>
    <el-col :span="5" class="middle">{{data.value.toFixed(2)}}</el-col>
    <el-col :span="5" class="right">{{data.rank}}/{{total}}</el-col>

  </el-row>
</template>

<script>
  export default {
    name: "HeroDetail",
    props: ['metric', 'data', 'total']
  }
</script>

<style scoped>
  .detail-row {
    border-bottom: .1rem solid #d9d9d9;
    font-size:1.2rem
  }

  .middle, .right {
    padding: .5rem 1rem;
    text-align: right;
  }

  .left {
    overflow-x: auto;
    padding: .5rem 1rem;
  }

  @media (max-width: 768px) {
    .detail-row {
      font-size: 0.85rem;
    }
  }
</style>
<style>

</style>



