<template>
  <el-switch
    style="display: block"
    v-model="innerStatus"
    active-color="#ff8c09"
    inactive-color="#13ce66"
    active-text="季后赛"
    inactive-text="常规赛" class="stage-switch">
  </el-switch>

</template>

<script>
  export default {
    name: "StageSwitch",
    data() {
      return {
        innerStatus: this.playoff
      }
    },
    props: {playoff: Boolean},
    watch: {
      innerStatus: function (v) {
        this.$emit('switch-changed', v)
      }
    },
    model: {
      prop: 'playoff',
      event: 'switch-changed'
    },

  }
</script>

<style>
  .stage-switch {
    margin: 10px 0;
  }

  .stage-switch .el-switch__label * {
    font-size: 16px;
  }

  .stage-switch .el-switch__label.el-switch__label--right.is-active {
    color: #ff8c09
  }

  .stage-switch .el-switch__label.el-switch__label--left.is-active {
    color: #13ce66
  }

</style>



