<template>
  <div>
    <stage-switch v-model="isPlayoff"></stage-switch>
    <div v-loading="player===undefined||detail===undefined" class="player-detail">
      <div v-if="player!==undefined&&detail!==undefined">
        <div ref="header" class="player-header"
             :style="{backgroundColor:`${player.team.colors.primary.color}`,color:isBrightBg?'black':'white'}">
          <div style="display: flex;align-items: flex-end;">
            <div class="player-img-container">
              <img :src="player.basic.headshot" width="100%" class="player-img">
            </div>
            <div style=" padding:1rem">
              <div class="player-name">{{player.basic.name}}</div>
              <div class="player-name-real">{{player.basic.fullName}}</div>
              <router-link :to="'/team/'+player.team.id+'/'+player.team.name">
                <div style="display: flex;align-items: flex-end;margin-top: 1rem">
                  <img :src="$teamLogos[player.team.id]" class="player-team-logo">
                </div>
              </router-link>
            </div>
          </div>
        </div>
        <el-tabs type="border-card" value="detail" class="tab-outside">
          <el-tab-pane lazy label="英雄详情" name="detail">
            <el-table
              :data="player.heroes"
              :default-sort="{prop: 'time_played_total', order: 'descending'}">
              <el-table-column
                prop="hero"
                label="英雄"
                fixed
                sortable>
                <template slot-scope="scope">
                  <div class="hero-portrait-image-mask">
                    <div :style="`background-image: url(${getHeroIcon(scope.row.hero)})`"
                         class="hero-portrait-image"></div>
                  </div>
                </template>
              </el-table-column>
              <el-table-column
                prop="eliminations_avg_per_10m"
                label="消灭"
                :formatter="toFixed2"
                sortable>
              </el-table-column>
              <el-table-column
                prop="deaths_avg_per_10m"
                label="阵亡"
                :formatter="toFixed2"
                sortable
              >
              </el-table-column>
              <el-table-column
                prop="hero_damage_avg_per_10m"
                label="伤害"
                :formatter="toFixed0"
                sortable>
              </el-table-column>
              <el-table-column
                prop="healing_avg_per_10m"
                label="治疗"
                :formatter="toFixed0"
                sortable>
              </el-table-column>
              <el-table-column
                prop="ultimates_earned_avg_per_10m"
                :formatter="toFixed2"
                label="终极技能"
                sortable
                width="120">
              </el-table-column>
              <el-table-column
                prop="final_blows_avg_per_10m"
                :formatter="toFixed2"
                label="最后一击"
                sortable
                width="120">
              </el-table-column>
              <el-table-column
                prop="time_played_total"
                label="游戏时间"
                :formatter="formatTime"
                sortable
                width="150">
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane lazy label="所有英雄" name="all">
            <el-table
              :data="player.all"
            >
              <el-table-column
                prop="type"
                label=" "
                fixed
              >
              </el-table-column>
              <el-table-column
                prop="eliminations_avg_per_10m"
                label="消灭"
                :formatter="toFixed2">
              </el-table-column>
              <el-table-column
                prop="deaths_avg_per_10m"
                label="阵亡"
                :formatter="toFixed2">
              </el-table-column>
              <el-table-column
                prop="hero_damage_avg_per_10m"
                label="伤害"
                :formatter="toFixed0"
              >
              </el-table-column>
              <el-table-column
                prop="healing_avg_per_10m"
                label="治疗"
                :formatter="toFixed0"
              >
              </el-table-column>
              <el-table-column
                prop="ultimates_earned_avg_per_10m"
                :formatter="toFixed2"
                label="终极技能"
                width="120">
              </el-table-column>
              <el-table-column
                prop="final_blows_avg_per_10m"
                :formatter="toFixed2"
                label="最后一击"
                width="120">
              </el-table-column>
              <el-table-column
                prop="time_played_total"
                label="游戏时间"
                :formatter="formatTime"
                width="150">
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane lazy label="详细数据(Beta)" name="specific">
            <div v-if="detail">
              <el-select v-model="detailHero" placeholder="请选择" style="margin: 0.3rem 0">
                <el-option
                  v-for="(hero,index) in heroes"
                  :key="index"
                  :label="hero"
                  :value="hero">
                </el-option>
              </el-select>
              <el-table :data="Object.keys(detail[detailHero])" v-if="detail[detailHero]"
                        :default-sort="{prop: 'rank', order: 'descending'}">
                <el-table-column
                  label="指标"
                  min-width="3">
                  <template slot-scope="scope">
                  <span>
                    {{scope.row}}
                  </span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="数据"
                  min-width="1">
                  <template slot-scope="scope">
                  <span>
                    {{ranks[detailHero][scope.row][playerId].value.toFixed(2)}}
                  </span>
                  </template>
                </el-table-column>
                <el-table-column
                  prop="rank"
                  label="排名"
                  min-width="1"
                  sortable
                  :sort-method="sortMethod">
                  <template slot-scope="scope">
                  <span>
                    {{ranks[detailHero][scope.row][playerId].rank}}/{{Object.keys(ranks[detailHero][scope.row]).length}}
                  </span>
                  </template>
                </el-table-column>
              </el-table>

            </div>

          </el-tab-pane>

        </el-tabs>

      </div>
    </div>
    <div v-if="detail!==undefined" style="text-align: left">
    </div>
  </div>
</template>

<script>
  import {convertSec, isBright, heroIcon} from "@/common/util"
  import StageSwitch from "@/components/StageSwitch";
  import OWL from "@/common/owl";
  import HeroDetail from "@/components/Matches/HeroDetail";

  export default {
    name: "Player",
    components: {HeroDetail, StageSwitch},

    data() {
      return {
        player: undefined,
        isPlayoff: false,
        detailHero: 'all',
        playerId: this.$route.params.id,
      }
    },
    mounted() {
      this.fetchPlayer();
    },
    computed: {
      ranks() {
        return this.$store.getters.getRanks(this.season);
      },
      detail() {
        const players = this.$store.getters.getAllPlayersDetail(this.season)
        return players ? players[this.playerId] : undefined
      },
      heroes() {
        if (this.detail === undefined)
          return [];
        return Object.keys(this.detail)
      },
      season() {
        return this.$store.getters.getSeason
      },
      seasonAndStage() {
        return [this.season, this.isPlayoff]
      },
      isBrightBg() {
        return isBright(this.player.team.colors.primary.color.slice(1))
      }
    },
    watch: {
      '$route'(to, from) {
        if (to.name === "Player")
          this.fetchPlayer();
      },
      seasonAndStage: function () {
        this.fetchPlayer();


      }
    },
    methods: {
      sortMethod(metrics1, metrics2) {
        const p1 = this.ranks[this.detailHero][metrics1][this.playerId].rank;
        const total1 = Object.keys(this.ranks[this.detailHero][metrics1]).length;
        const p2 = this.ranks[this.detailHero][metrics2][this.playerId].rank;
        const total2 = Object.keys(this.ranks[this.detailHero][metrics2]).length
        return p2 / total2 - p1 / total1

      },
      fetchPlayer() {
        this.playerId = this.$route.params.id;
        this.detailHero = 'all';
        this.player = undefined;
        if (this.$store.getters.getAllPlayersDetail(this.season) === undefined)
          this.$store.dispatch('setPlayers', this.season);

        this.$owl.getPlayer(this.playerId, {
          season: this.season,
          stage: this.isPlayoff ? OWL.STAGE.POST : OWL.STAGE.REGULAR
        }).then(player => {
            this.player = player;
          }
        )
      },

      toFixed0(row, column, cellValue) {
        return cellValue === null ? 'NA' : Number.isInteger(cellValue) ? cellValue : cellValue.toFixed(0)
      },
      toFixed2(row, column, cellValue) {
        return cellValue === null ? 'NA' : Number.isInteger(cellValue) ? cellValue : cellValue.toFixed(2)
      },
      formatTime(row, column, cellValue) {
        const [h, m] = convertSec(cellValue);

        return cellValue === null ? 'NA' : h === 0 ?
          m === 0 ? `${Math.round(cellValue)}秒` : `${m}分钟`
          : `${h}小时${m}分钟`
      },
      getHeroIcon(heroName) {
        return heroIcon(heroName)
      }
    },

  }
</script>

<style scoped>
  .player-detail {
    margin: 0 auto;
    text-align: left;
    min-height: 250px;
  }

  .player-header {
    color: black;
  }


  .player-img {
    max-width: 454px;
  }

  .player-team-logo {
    width: 10rem;
    height: 10rem;
  }

  .player-name {
    font-size: 2.5rem;
  }

  .player-name-real {
    font-size: 1rem;
  }


  @media screen and (max-width: 760px) {


    .player-team-logo {
      width: 7rem;
      height: 7rem;
    }

    .player-name {
      font-size: 2rem;
    }

    .player-name-real {
      font-size: 0.7rem;
    }

  }

  .player-img-container {
    width: 700px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

</style>

<style>
  .player-detail .el-tabs--border-card > .el-tabs__content {
    padding: 0 10px
  }
</style>



